import React, { useLayoutEffect, useState } from "react";
import {
  MailOutlined,
  AppstoreOutlined,
  SettingOutlined,
} from "@ant-design/icons";
import { Layout, Menu, Button } from "antd";
import logo from "../assets/images/cendela_logo.svg";
const { Header, Content, Footer } = Layout;
const { SubMenu } = Menu;
interface NavbarProps {}

export const Navbar: React.FC<NavbarProps> = () => {
  const [current, setCurrent] = useState("mail");
  const [scroll, setScroll] = useState(0)


  onscroll=()=>{
      setScroll(window.scrollY)
  }
  useLayoutEffect(() => {
      
      return () => {
          
      };
  }, [])

  const handleClick = (e: any) => {
    console.log("click ", e);
    setCurrent(e.key);
  };
  return (
    <Header className={"navbar "+(scroll===0?"on-top":"")}>
      <div className="logo">
        <img src={logo} height={40} />
      </div>
      <Menu
        // onClick={handleClick}
        // selectedKeys={[current]}
        mode="horizontal"
        className={"navbar-menu on-top"}
      >
        <Menu.Item key="mail" className={""+(scroll===0?"nav-item":"")}>Articles</Menu.Item>
        <Menu.Item key="app" className={""+(scroll===0?"nav-item":"")}>Sign In</Menu.Item>
        <Menu.Item key="signup" className="nav-item">
          <Button type="primary" size="large">
            Sign Up
          </Button>
        </Menu.Item>
      </Menu>
    </Header>
  );
};
